.recommended_section { background-color: #f8f8f8; padding-bottom: 15px; }

.recommended_section h2 { margin: 0; font-size: 16px; line-height: 18px; font-weight: bold; color: #4d4d4d; padding: 36px 0; }

.recommended_block { overflow: hidden; }

.recommended_list { overflow: auto; padding-bottom: 20px !important; }

